import React, { Component } from 'react';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import Navbar from './components/Navbar';
import Splash from './components/Splash';
import Portfolio from './components/Portfolio';
import DigitalMarketing from './components/DigitalMarketing';
import ProductionProcess from './components/ProductionProcess';
import WhyInfiniteFrontier from './components/WhyInfiniteFrontier';
import Faq from './components/Faq';
import Footer from './components/Footer';

import styles from './Landing.module.scss';

const splash = getImage('landing.splash');
const greenWavy = getImage('landing.green_wavy');
const ellipse = getImage('landing.ellipse');

class Landing extends Component {
  render() {
    return (
      <div className={styles.landing}>
        <div className={styles.landingSplash}>
          <Image {...splash} />
        </div>
        <div className={styles.greenWavy}>
          <Image {...greenWavy} />
        </div>
        <div className={styles.ellipse}>
          <Image {...ellipse} />
        </div>
        <Navbar />
        <Splash />
        <Portfolio />
        <DigitalMarketing />
        <ProductionProcess />
        <WhyInfiniteFrontier />
        <Faq />
        <Footer />
      </div>
    );
  }
}

export default Landing;
