import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import { Button } from 'reactstrap';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import SegmentHeader from '../SegmentHeader';

import styles from './ProductionProcess.module.scss';

const purpleWave = getImage('landing.purple_wave');
const curveArrow = getImage('landing.curve_arrow');
const whyBG = getImage('landing.why_bg');

const data = [{
  title: 'Ideation',
  desc: 'Give us your creative brief, then we’ll script it and set our experts to work. Along with a storyboard, we’ll craft a dialed and detailed plan of action surrounding the creation of your video.',
}, {
  title: 'Voiceover',
  desc: 'Now comes the fun part. We’ll record the first draft voiceover using a variety of voice types. Tell us what works and what doesn’t so that we get it exactly right.',
}, {
  title: 'Preview',
  desc: 'We’ll storyboard the script before reaching out for your approval. Check out custom character design, color mood, and other graphic elements coming together for your video. Give us a thumbs-up, and we’ll proceed to...',
}, {
  title: 'Animation',
  desc: 'Let’s bring your story to life! During the animation phase, we take the previous elements and give them a breath of 2D animation. Custom music, sound effects, a stellar voiceover, and pro pacing give your video attention-grabbing movement.',
}, 
// {
//   title: 'Render',
//   desc: 'It is no secret that Facebook prioritizes video in its. It is no secret that Facebook prioritizes video in its algorithm, allowing you to reach up to 50% more  people at lower costs than other advertisers. While your competitors are still using image ads shouting discounts & offers on Facebook, think about what a video ad with can do for your brand?',
// }
]

class ProductionProcess extends Component {
  renderData(d, index) {
    const width = 100 / data.length;

    return (
      <div
        key={`production-process-${index}`}
        className={styles.dataContainer}
        style={{width: `${width}%`}}
      >
        <div className={styles.dataInner}>
          <h3>{d.title}</h3>
          <hr />
          <p>{d.desc}</p>
          {
            index < data.length - 1 && (
              <div className={styles.curveContainer}>
                <Image {...curveArrow} />
              </div>
            )
          }
        </div>
      </div>
    )
  }

  render() {
    return (
      <div id="process" className={styles.productionProcess}>
        <SegmentHeader
          title="How It Works"
          subTitle="PROCESS"
        />
        <div className={styles.purpleWaveContainer}>
          <Image {...purpleWave} />
        </div>
        <div className={[styles.tagline, 'main-margin'].join(' ')}>
          <p>Partnering with us to create an explainer video for your business is simple and downright exciting.</p>
        </div>
        <div className={[styles.dataWrapper, 'main-margin'].join(' ')}>
          {data.map(this.renderData)}
        </div>
        <p className={[styles.tagline2, 'main-margin'].join(' ')}>That’s all there is to it! Sound good? Great — get the ball rolling by <Button
            color="info"
            onClick={() => {
              try {
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
                console.log(error);
              }
            }}
          ><span>contacting us</span></Button> today.</p>
        <div className={styles.whyImageContainer}>
          <Image {...whyBG} />
        </div>
      </div>
    );
  }
}

ProductionProcess.defaultProps = {
};

ProductionProcess.propTypes = {
};

export default ProductionProcess;
